.board-list{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    @include sizeCtl(525,216);
    .board-item{
        position: absolute;
        top: 0;
        left: 0;
        @include sizeCtl(525,216);
        .board{
            @include sizeCtl(525,216);
        }
        .svg-pipe{
            overflow: visible;
        }
        .item{
            &.base{
                @include sizeCtl(436,270);
                top: 0;
                left: 0;
                z-index: 1;
            }
            &.wall{
                @include sizeCtl(196,193);
                @include itemLayout(34,49);
                z-index: 11;
            }

            &.sofa_01{
                @include sizeCtl(36,41);
                @include itemLayout(72,302);
                z-index: 2;
            }

            &.sofa_02{
                @include sizeCtl(36,41);
                @include itemLayout(94,340);
                z-index: 2;
            }

            &.sofa_03{
                @include sizeCtl(36,41);
                @include itemLayout(138,264);
                z-index: 2;
            }

            &.desk{
                @include sizeCtl(121,97);
                @include itemLayout(72,251);
                z-index: 3;
            }

            &.led{
                @include sizeCtl(34,153);
                @include itemLayout(-94,76);
                z-index: 20;
            }
        }
        &.board-item-01{
            @include itemLayout(81,1533);
            .board-name{
                .board-text{
                    display: block;
                }
                .board-bg{
                    background-color:$color-board-green;
                    .skew-block{
                        display: block;
                    }
                }
            }
            .svg-pipe{
                position: absolute;
                @include sizeCtl(356,220);
                @include itemLayout(-40,-266);
                z-index: 12;
            }
        }
        &.board-item-02{
            @include itemLayout(788,1533);
            .board{
                @include itemLayout(-124,-10);
            }
            .svg-pipe{
                position: absolute;
                z-index: 12;
                @include sizeCtl(412,323);
                @include itemLayout(-146,-328);
            }
        }
        &.board-item-03{
            @include itemLayout(61,-350);
            .board{
                @include itemLayout(0,0);
            }
            .svg-pipe{
                position: absolute;
                @include sizeCtl(356,220);
                @include itemLayout(8,220);
                z-index: 12;
            }
        }
        &.board-item-04{
            @include itemLayout(726,-370);
            .board{
                @include itemLayout(0,0);
            }
            .svg-pipe{
                position: absolute;
                @include sizeCtl(356,220);
                @include itemLayout(-2,220);
                z-index: 12;
            }
        }
    }
}
.board{
    position: relative;
    .item{
        position: absolute;
    }
}