.test-box{
    width: 100%;
    height: 50px;
    position: fixed;
    left: 0;
    bottom : 0;
    background-color: rgba(0,0,0,0.3);
    z-index: 100;
    .button{
        background-color: #000000;
        color: #FFFFFF;
        margin-left: 30px;
        margin-top: 16px;
        padding: 4px 8px;
        border-radius: 6px;
        font-weight: bold;
        cursor: pointer;
    }
}