#pechiko-list{
    position: absolute;
    @include itemLayout(700,370);
    z-index: 9;
    .pechiko-item{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i}{
                @include itemLayout(38 * ($i) ,68 * ($i))
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 5}{
                @include itemLayout((38 * ($i)) - 70 ,68 * ($i))
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 11}{
                @include itemLayout(38 * ($i) ,(-68 * ($i)) + 790 )
            }
        }
        @for $i from 1 through 6 {
            &.pechiko-item-#{$i + 17}{
                @include itemLayout((38 * ($i)) - 50 ,(-68 * ($i)) + 710 )
            }
        }


    }
}


/*スプライトアニメーション*/
.pechiko{
    position: absolute;
    width: 60px;
    height: 60px;
    background-image: url('../images/man-bike.png');
    &.bike1{
        animation: sprite1 0.5s steps(7, start) infinite;
    }
    &.bike2{
        animation: sprite2 0.5s steps(7, start) infinite;
    }
    &.bike3{
        animation: sprite3 0.5s steps(7, start) infinite;
    }
    &.bike4{
        animation: sprite4 0.5s steps(7, start) infinite;
    }
    &.bike5{
        transform: rotateY(180deg);
        animation: sprite1 0.5s steps(7, start) infinite;
    }
    &.bike6{
        transform: rotateY(180deg);
        animation: sprite2 0.5s steps(7, start) infinite;
    }
    &.bike7{
        transform: rotateY(180deg);
        animation: sprite3 0.5s steps(7, start) infinite;
    }
    &.bike8{
        transform: rotateY(180deg);
        animation: sprite4 0.5s steps(7, start) infinite;
    }
}

@keyframes sprite1 {
    0%   {background-position: 0px 420px;}
    100% {background-position: 0px 0px;}
}

@keyframes sprite2 {
    0%   {background-position: 64px 420px;}
    100% {background-position: 64px 0px;}
}

@keyframes sprite3 {
    0%   {background-position: 180px 420px;}
    100% {background-position: 180px 0px;}
}

@keyframes sprite4 {
    0%   {background-position: 360px 420px;}
    100% {background-position: 360px 0px;}
}
