
.field{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    @include sizeCtl(1621,1038);
    z-index: 4;
}

.ground{
    position: absolute;
    top: 0;
    @include sizeCtl(1621,1038);
    z-index: 9;
}

.field-gnd{
    position: absolute;
    left: 0;
    &.front{
        top: 0;
        @include sizeCtl(1621,1038);
        z-index: 10;
    }
    &.side{
        z-index: 8;
        @include sizeCtl(1078,642);
        top: 292px;
        left: 134px;
    }
}

.field{
    .field-list{
        position: relative;
        @include sizeCtl(1621,1038);
        z-index: 10;
        overflow: hidden;
        .field-item{
            position: absolute;
            z-index: 2;
            &.river {
                @include itemLayout(340,172);
                .item01{
                    @include sizeCtl(920,472);
                }
            }
            &.train{
                @include itemLayout(-35,705);
                z-index: 2;
                .item01{
                    @include sizeCtl(280,227);
                }
            }

            &.store{
                @include itemLayout(110,990);
                .item01{
                    @include sizeCtl(175,170);
                }
            }
            &.tower{
                @include itemLayout(430,700);
                .item01{
                    @include sizeCtl(111,329);
                }
            }
            &.wood01{
                @include itemLayout(384,1134);
                &.wood01_01{
                    @include itemLayout(0,720);
                    z-index: 2;
                }
                &.wood01_02{
                    @include itemLayout(740,877);
                    z-index: 2;
                }
                &.wood01_03{
                    @include itemLayout(371,172);
                    z-index: 2;
                }
                &.wood01_04{

                    @include itemLayout(487,220);
                    z-index: 3;
                }
                .item01{
                    @include sizeCtl(96,95);
                }
            }
            &.wood02{
                &.wood02_01{
                    @include itemLayout(630,600);
                    z-index: 2;
                }
                &.wood02_02{
                    @include itemLayout(370,1130);
                    z-index: 2;
                }
                &.wood02_03{
                    @include itemLayout(236,558);
                    z-index: 3;
                }
                &.wood02_04{
                    @include itemLayout(390,1160);
                    z-index: 2;
                }
                &.wood02_05{
                    @include itemLayout(102,972);
                    z-index: 2;
                }
                .item01{
                    @include sizeCtl(109,134);
                }
            }
            &.building01{
                @include itemLayout(417,1050);
                z-index: 3;
                .item01{
                    @include sizeCtl(145,225);
                }
            }
            &.building02{
                @include itemLayout(420,875);
                z-index: 3;
                .item01{
                    @include sizeCtl(145,225);
                }
            }
            &.road{
                @include itemLayout(222,268);
                z-index: 1;
                .item01{
                    @include sizeCtl(1243,433);
                }
            }
            &.bridge{
                @include itemLayout(450,418);
                z-index: 3;
                .item01{
                    @include sizeCtl(150,120);
                }
            }
            &.light{
                .item01{
                    @include sizeCtl(40,99);
                }
                &.light01{
                    @include itemLayout(58,900);
                    z-index: 3;
                }
                &.light02{
                    @include itemLayout(154,740);
                    z-index: 3;
                }
                &.light03{
                    @include itemLayout(245,365);
                    z-index: 1;
                }
                &.light04{
                    @include itemLayout(590,1180);
                    z-index: 4;
                }
                &.light05{
                    @include itemLayout(470,850);
                }
            }
            &.rail{
                @include itemLayout(60,859);
                position: relative;
                .item{
                    @include sizeCtl(90,58);
                    position: absolute;
                    @for $i from 1 through 16 {
                        &.item#{$i} {
                            @include itemLayout(32 * ($i - 1) ,-52 * ($i - 1))
                        }
                    }
                }

            }
        }
    }
}
