/*棒人間スプライトアニメーション*/
.ningen-list{
    position: absolute;
    top: 0;
    left: 160px;
    z-index: 10;
    .ningen-item{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;
    }
}

.ningen{
    position: absolute;
    width: 60px;
    height: 60px;
    background-image: url('../images/man-normal.png');
    overflow: hidden;
    &.drop1{
        background-position: 0 0;
        animation: drop-sprite1 0.5s steps(3) infinite;
    }
    &.drop2{
        background-position: 180px 0;
        animation: drop-sprite2 2s steps(13) forwards;
    }
    &.walk1{
        background-position: 360px 0;
        animation: walk-sprite1 0.5s steps(7) infinite;
    }
    &.walk2{
        background-position: 540px 0;
        animation: walk-sprite2 0.5s steps(7) infinite;
    }
}

@keyframes drop-sprite1 {
    100% {background-position: 0 0;}
    0%   {background-position: 0 -180px;}
}

@keyframes drop-sprite2 {
    0% {background-position: 180px 0;}
    100%   {background-position: 180px -780px;}
}

@keyframes walk-sprite1 {
    100% {background-position: 360px 0;}
    0%   {background-position: 360px -420px;}
}

@keyframes walk-sprite2 {
    100% {background-position: 540px 0;}
    0%   {background-position: 540px -420px;}
}
