body,html{
    position: relative;
    height: 100%;
    margin: 0 0;
}

.stage{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 178px;
    height: 138px;
}

.block{
    position: absolute;
    width: 89px;
    height: 69px;
    background: url(../images/block.png) no-repeat;
    &.block-01{
        top: 69px;
        left : 0;
    }
    &.block-02{
        top: 0;
        left: 89px;
    }
    &.block-03{
        top: 69px;
        left: 89px;
    }
    &.block-04{

    }
}
